import {useGameContext} from '../../../../provider/game/game-context';
import {UiButton} from '../../../../layout/ui-button/ui-button';
import {GameActionType, GameActionTypeEnum} from '../../../../provider/game/game-context-type';
import {clientGameImage} from '../image/client-game-image';

import clientGameMoveBackButtonStyle from './client-game-move-back-button.scss';

export function ClientGameMoveBackButton(): JSX.Element {
    const {lastMoveAction, pushAction, isEnableEndTurnButton} = useGameContext();

    const actionData: GameActionType | null =
        lastMoveAction && isEnableEndTurnButton
            ? {
                  actionType: GameActionTypeEnum.unitMoveRevert,
                  addUnitList: lastMoveAction.addUnitList,
                  buyUnitType: lastMoveAction.buyUnitType,
                  movePointList: [...lastMoveAction.movePointList].reverse(),
                  toPoint: lastMoveAction.unitCurrentPoint,
                  unitCurrentPoint: lastMoveAction.toPoint,
                  unitId: lastMoveAction.unitId,
              }
            : null;

    return (
        <div className={clientGameMoveBackButtonStyle.client_game_move_back_button}>
            <UiButton
                iconSrc={clientGameImage.moveRevert.src}
                isDisabled={!actionData}
                isSquared
                langKey="GAME__REVERT_MOVE"
                onClick={async () => {
                    if (actionData) {
                        await pushAction(actionData);
                    }
                }}
            />
        </div>
    );
}
